import axios from 'axios'
export default {
  name: 'assistantForm',
  data () {
    return {
      item: {
        name: '',
        creator: '',
        start_time: '',
        end_time: '',
        content: '',
        leader: '',
        targets: [],
        participants: []
      },
      list: [],
      target: ''
    }
  },
  methods: {
    getTarget () {
      this.item.targets = this.target.split('，')
    },
    getLeaderList () {
      axios.get(this.url + '/user/level/4')
        .then(this.getLeaderListSucc)
    },
    getLeaderListSucc (res) {
      res = res.data
      if (res) {
        this.LeaderList = res
        this.list = this.list.concat(this.LeaderList)
      }
    },
    getFullList () {
      axios.get(this.url + '/user/level/5')
        .then(this.getFullListSucc)
    },
    getFullListSucc (res) {
      res = res.data
      if (res) {
        this.FullList = res
        this.list = this.list.concat(this.FullList)
      }
    },
    getStudentList () {
      axios.get(this.url + '/user/level/2')
        .then(this.getStudentListSucc)
    },
    getStudentListSucc (res) {
      res = res.data
      if (res) {
        this.StudentList = res
      }
    },
    submit () {
      this.$axios({
        method: 'post',
        url: this.url + '/project/',
        data: JSON.stringify(this.item)
      }).then((res) => {
        console.log(res)
        alert('成功创建项目！')
      }).catch((err) => {
        alert(err)
      })
    }
  },
  mounted () {
    this.getLeaderList()
    this.getStudentList()
    this.getFullList()
  }
}



