import Assistant from './assistant/assistant'
import Teacher from './teacher/teacher'
import Others from './others/others'

export default {
  name: 'newProject',
  components: {
    Assistant,
    Teacher,
    Others
  },
  data () {
    return {
      level: localStorage.getItem('level')
      // level: 3
    }
  },
  methods: {
    judge () {
      if (!localStorage.getItem('id')) {
        this.$router.push('/')
        alert('您还没有绑定用户哦！')
      }
    }
  },
  created () {
    this.judge()
  }
}



